                    </div>
                    <p class="page-footer__title-text">איטרניטי מציעים לכם דרך חדשה ומיוחדת להנצחה. פלטפורמה דיגיטלית
                        מתקדמת ברוח הזמן, המאפשרת לכל אחד ליצור בקלות מפעל הנצחה אישי, מכובד ורב תחומי. החל מרגע הבשורה
                        הקשה ויצירת מודעת אבל לשיתוף ולפרסום, דרך רכישת מצבה באופן מכובד, אישי לטפל בעניין המחיר..יותר
                        טוב מהקיים. וראשון מסוגו. ועד הקמת אתר אישי, יצירת “כספת” של זיכרונות המאפשרת במילים, בקול,
                        בתמונות, בסרטים, לנצור לנצח נכסי זיכרון.</p>
